<template>
  <div class="model-mask">
    <div class="cropperContent">
      <vue-cropper
        ref="cropper"
        :img="cropProps.img"
        :outputSize="option.size"
        :outputType="option.outputType"
        :info="true"
        :full="option.full"
        :canMove="option.canMove"
        :canMoveBox="option.canMoveBox"
        :original="option.original"
        :autoCrop="option.autoCrop"
        :fixed="option.fixed"
        :fixedNumber="option.fixedNumber"
        :centerBox="option.centerBox"
        :infoTrue="option.infoTrue"
        :fixedBox="option.fixedBox"
        @realTime="realTime"
      ></vue-cropper>
      <div class="button-box">
        <button class="custom-btn btn-11" @click="onSelect"></button>
        <button class="custom-btn btn-2" @click="onBack"></button>
      </div>
    </div>
  </div>
</template>

<script>
import { VueCropper } from "vue-cropper";

export default {
  components: {
    VueCropper,
  },

  props: {
    cropProps: {},
  },
  data() {
    return {
      //option
      option: {
        img: "", // 
        info: true, // 
        outputSize: 1, // 
        outputType: "jpeg", // 
        canScale: true, // 
        autoCrop: true, // 
        autoCropWidth: 400, // 
        autoCropHeight: 300, // 
        fixedBox: false, //  
        fixed: true, // 
        fixedNumber: [4, 3], // 
        full: false, // 
        canMoveBox: true, // 
        original: false, // 
        centerBox: true, // 
        infoTrue: true, // true  false 
      },
    };
  },
  methods: {
    ///
    changeScale(num) {
      console.log("changeScale");
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    }, //
    rotateLeft() {
      console.log("rotateLeft");
      this.$refs.cropper.rotateLeft();
    }, //
    rotateRight() {
      console.log("rotateRight");
      this.$refs.cropper.rotateRight();
    }, // 
    realTime(data) {
      this.previews = data;
    },
    imgLoad(msg) {
      console.log(msg);
    },
    cropImage() {},
    onSelect() {
      this.$refs.cropper.getCropBlob((data) => {
        var img = window.URL.createObjectURL(data);
        this.$emit("setCropImg", {
          status: false,
          img,
          id: this.cropProps.id,
        });
      });
    },
    onReset(param) {
      this.target = param.target;
      this.option.img = param.url;
      this.preview = param.url;
    },
    onBack() {
      this.$emit("changeCropModel", {
        status: false,
        img: "",
        id: "",
      });
    },
  },
};
</script>

